body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #cad3c8;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  position: relative;
  overflow: hidden;
}
.nav {
  width: 100%;
  height: 100vh;
  padding: 20px;
  display: flex;
  flex-direction: column;
  background: lime;
}

.routBlock {
  width: 1500px;
  height: 600px;
  border-radius: 10px;
  display: grid;
  align-items: center;
}
.one {
  background: #b53471;
}
.two {
  background: #006266;
}
.three {
  background: #ea2027;
}
.navigate {
  position: absolute;
  top: 20px;
  left: 20px;
}
.navigate_item {
  margin-right: 5px;
}
