.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

.fullCv {
  width: 700px;
  margin: 0 auto;
  height: 90vh;
  box-shadow: -10px 11px 11px 4px rgba(34, 60, 80, 0.42);
  box-sizing: border-box;
  display: flex;
  font-family: Arial, Helvetica, sans-serif;
  position: relative;
  transition: transform 0.8s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.fullCv__left {
  box-sizing: border-box;
  height: 100%;
  flex: 0.3 1;
  background-color: #2c3a47;
  padding-right: 25px;
  padding-left: 25px;
  text-align: left;
  /* padding-top: 50px; */
  overflow: scroll;
  position: relative;
}

.avatar {
  width: 125px !important;
  height: 125px !important;
  border: 3px solid whitesmoke;
  margin: 0 auto;
}
.avatar__container {
  position: -webkit-sticky !important;
  position: sticky !important;
  padding-top: 25px;
  padding-bottom: 25px;
  top: 0;
  background-color: #2c3a47;
  z-index: 10;
}
.contacts {
  margin-top: 50px;
  border-bottom: 1px solid white;
  padding-bottom: 25px;
}
.contacts__option {
  display: flex;
  align-items: center;
}
.contacts__option > span {
  margin-left: 20px;
  font-size: 12px;
  color: lightgray;
  letter-spacing: 0.3px;
}
.contacts__option > span:hover {
  color: white;
  cursor: pointer;
}
.education {
  margin-top: 25px;
  border-bottom: 1px solid white;
  padding-bottom: 25px;
}
.education h3 {
  margin-bottom: 10px;
  margin-top: 10px;
  letter-spacing: 0.5px;
  font-size: 14px;
}
.edication__item p {
  font-style: italic;
  font-size: 11px;
  color: lightgray;
  margin-bottom: 5px;
  letter-spacing: 0.6px;
}
.edication__item p span {
  color: white;
}
.skills {
  margin-top: 25px;
}

.skills__list ul {
  list-style-type: circle;
  margin-left: 20px;
}
.skills__list ul li {
  font-style: italic;
  font-size: 13px;
  color: lightgray;
  margin-bottom: 5px;
  letter-spacing: 0.6px;
}
.blockTitle__h2 {
  color: white;
  position: relative;
  letter-spacing: 2px;
  margin-bottom: 30px;
}
.blockTitle__h2::after {
  content: "";
  display: block;
  position: absolute;
  height: 1px;
  width: 35px;
  background-color: white;
  margin-top: 10px;
}
.fullCv__right {
  height: 100%;
  flex: 0.7 1;
  background-color: white;
  padding-left: 15px;
  box-sizing: border-box;
  overflow: scroll;
}

.fullCv__right_header h2::after {
  content: " ";
  display: block;
  position: absolute;
  height: 1px;
  width: 40%;
  left: 50%;
  top: 35px;
  transform: translate(-50%);
  background-color: grey;
}

.fullCv__right::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: #2c3e50;
}
.fullCv__right::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 6px;
}
.fullCv__left::-webkit-scrollbar-thumb {
  border-radius: 3px;
  display: none;
  background-color: #2c3e50;
}
.fullCv__left::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 6px;
  display: none;
}

