$accent-color: #07e8ff;
$base-color: #2f0000;
$border-size: 3px;
.check {
    display: flex;
    width: 100%;
    position: absolute;
    z-index: 10; }
.checkout_left {
    width: 40%;
    height: 100vh;
    background: linear-gradient(180deg,#3c0453, $base-color); }

.checkout_right {
    width: 60%;
    height: 100vh;
    background: linear-gradient(180deg,#3c0453, $base-color); }
